@import 'variables';
@import 'reset';

html, body {
    font-size: 100%;
    font-family: $primary-font;
}

::selection {
    background-color: $secondary-color;
    color: $primary-color;
}

.btn-whatsapp-site {
    position: fixed;
    width: 65px;
    height: 65px;
    bottom: 95px;
    right: 40px;
    z-index: 100;
    @media (max-width: 480px) {
        width: 60px;
        height: 60px;
        bottom: 45px;
        right: 10px;
    }
    a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        background-color: #25d366;
        border-radius: 100%;
        font-size: 2.25em;
        color: white;
        transition: .3s;
        &:hover {
            transform: scale(1.1);
            box-shadow: 0 0 0 10px rgba(37, 211, 102, .3);
        }
    }
}

.main-btns {
    display: inline-block;
    padding: .45em 1.5em;
    margin: 1em 0;
    font-size: 1em;
    background-color: $secondary-color;
    border-radius: 50px;
    color: $primary-color;
    font-weight: 600;
    transition: .3s;
    @media (max-width: 480px) {
        font-size: .875em;
    }
    &:hover {
        background-color: $primary-color;
        color: $secondary-color;
        box-shadow: 0 0 0 5px rgba(141, 216, 21, .25);
    }
    &--big {
        margin: 1.5em 0;
        padding: .5em 1.5em;
        font-size: 1.125em;
        @media (max-width: 480px) {
            margin: 1em 0;
            padding: .35em 1.5em;
            font-size: 1em;
        }
    }
    &--black {
        background-color: $primary-color;
        color: white;
        &:hover {
            background-color: $secondary-color;
            color: $primary-color;
        }
    }
}

.titles {
    h1 {
        position: relative;
        display: inline-block;
        padding: 0 0 .75em;
        font-size: 2em;
        color: $gray-txt;
        font-weight: 600;
        @media (max-width: 480px) {
            font-size: 1.5em;
        }
        &:after {
            content: "";
            position: absolute;
            width: 70px;
            height: 5px;
            background-color: $secondary-color;
            border-radius: 50px;
            bottom: 16px;
            right: 0;
            @media (max-width: 480px) {
                width: 50px;
                bottom: 8px;
            }
        }
    }
    &--white {
        h1 {
            color: white;
        }
    }
}

.lity {
    background: rgba(0, 0, 0, .8);
}

header {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    transition: .3s;
    @media (max-width: 1025px) {
        background-color: white;
    }
    .row-header {
        padding: .5em 0;
        align-items: center;
        @media (max-width: 1025px) {
            padding: .35em 0;
        }
        &__col {
            &--logo {
                a {
                    display: inline-block;
                    visibility: hidden;
                    opacity: 0;
                    transition: .3s;
                    @media (max-width: 1025px) {
                        visibility: visible;
                        opacity: 1;
                    }
                    img {
                        display: block;
                        width: 120px;
                        @media (max-width: 1025px) {
                            width: 90px;
                        }
                    }
                }
            }
            &--menu {
                @media (max-width: 1025px) {
                    display: flex;
                    justify-content: flex-end;
                }
                .btn-colapse-xs {
                    position: relative;
                    width: 30px;
                    height: 21px;
                    flex-direction: column;
                    justify-content: space-between;
                    display: none;
                    cursor: pointer;
                    @media (max-width: 1025px) {
                        display: flex;
                    }
                    @media (max-width: 768px) {
                        margin-right: 1em;
                    }
                    .line {
                        display: block;
                        width: 30px;
                        height: 3px;
                        background-color: $primary-color;
                        border-radius: 50px;
                        transition: .3s;
                    }
                    &.open {
                        justify-content: center;
                        align-items: center;
                        .line {
                            position: absolute;
                            &--l1 {
                                transform: rotate(45deg);
                            }
                            &--l2 {
                                opacity: 0;
                            }
                            &--l3 {
                                transform: rotate(-45deg);
                            }
                        }
                    }
                }
                .nav-menu {
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    @media (max-width: 1025px) {
                        display: none;
                        position: fixed;
                        width: 100%;
                        height: 100vh;
                        background-color: #f2f2f2;
                        top: 73px;
                        left: 0;
                        z-index: 200;
                        overflow: auto;
                    }
                    &__item {
                        padding-left: 1.5em;
                        @media (max-width: 1025px) {
                            padding: 0;
                            border-bottom: 1px solid $secondary-color;
                        }
                        a {
                            display: block;
                            color: white;
                            transition: .3s;
                            @media (max-width: 1025px) {
                                padding: 2em 15px;
                                color: $primary-color;
                                font-weight: 600;
                            }
                            &:hover {
                                color: $secondary-color;
                            }
                            i {
                                display: block;
                                font-size: 1.25em;
                            }
                        }
                        &--map {
                            padding: 2em 0 2em 15px;
                            a {
                                display: block;
                                background-color: #ff6347;
                                box-shadow: 0 0 0 6px rgba(255, 99, 71, .35);
                                align-items: center;
                                padding: .3em 1em;
                                border-radius: 50px;
                                font-weight: 600;
                                font-size: 15px;
                                color: white !important;
                                @media (max-width: 1025px) {
                                    display: inline-block;
                                    font-size: 16px;
                                    font-weight: 700;
                                }
                                &:hover {
                                    background-color: $gray-txt;
                                    box-shadow: 0 0 0 6px rgba(51, 51, 51, .35);
                                }
                            }
                        }
                        &--whatsapp {
                            @media (max-width: 1025px) {
                                display: flex;
                                border: none;
                                padding: 2em 0 0;
                                margin-left: 15px;
                            }
                            a {
                                display: flex;
                                background-color: #25d366;
                                align-items: center;
                                padding: .3em 1em;
                                border-radius: 50px;
                                font-weight: 600;
                                font-size: 15px;
                                color: #103928;
                                &:hover {
                                    color: $primary-color;
                                    box-shadow: 0 0 0 6px rgba(37, 211, 102, .35);
                                }
                                i, span {
                                    display: block;
                                }
                                i {
                                    margin-right: .35em;
                                }
                            }
                        }
                    }
                    &__social {
                        .nav-social {
                            display: flex;
                            align-items: center;
                            @media (max-width: 1025px) {
                                padding: 1em 15px 0;
                            }
                            li {
                                padding-left: 1.25em;
                                @media (max-width: 1025px) {
                                    padding-left: 0;
                                    padding-right: 1em;
                                }
                                &:first-child {
                                    display: none;
                                    @media (max-width: 1025px) {
                                        display: block;
                                    }
                                }
                                a {
                                    font-size: 1.25em;
                                    color: white;
                                    transition: .3s;
                                    @media (max-width: 1025px) {
                                        font-size: 1.5em;
                                        color: $primary-color;
                                    }
                                    &:hover {
                                        color: $secondary-color;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &.scroll {
        background-color: white;
        box-shadow: 0 2px 16px rgba(0, 0, 0,.1);
        .row-header {
            &__col {
                &--logo {
                    a {
                        opacity: 1;
                        visibility: visible;
                    }
                }
                &--menu {
                    .nav-menu {
                        &__item {
                            a {
                                color: $gray-txt;
                                &:hover {
                                    color: $secondary-color;
                                }
                            }
                            &--whatsapp {
                                a {
                                    color: #103928;
                                    &:hover {
                                        color: #103928;
                                    }
                                }
                            }
                        }
                        &__social {
                            .nav-social {
                                li {
                                    a {
                                        color: $gray-txt;
                                        &:hover {
                                            color: $secondary-color;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.top-bg {
    position: relative;
    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(25, 68, 57, .65);
        top: 0;
        left: 0;
        z-index: 1;
    }
    &__slide {
        .slide-item-bg {
            height: 100vh;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            @media (max-width: 480px) {
                height: 90vh;
            }
        }
    }
    &__txt {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        padding: 1em;
        top: 0;
        left: 0;
        z-index: 10;
        color: white;
        text-align: center;
        img {
            display: inline-block;
            margin: 0 0 1em;
            width: 250px;
            @media (max-width: 1025px) {
                width: 200px;
            }
        }
        h1 {
            padding: 0 0 .5em;
            font-size: 3em;
            text-transform: uppercase;
            font-weight: 700;
            @media (max-width: 480px) {
                font-size: 2em;
            }
        }
        p {
            padding: 0;
            font-size: 1.25em;
            font-weight: 300;
            @media (max-width: 480px) {
                font-size: 1em;
            }
            a {
                color: white;
                text-decoration: underline;
                transition: .3s;
                @media (max-width: 480px) {
                    display: block;
                }
                i {
                    display: inline-block;
                    padding-right: .25em;
                }
                &:hover {
                    color: $secondary-color;
                }
            }
        }
        .btns-top {
            display: flex;
            align-items: center;
            a {
                display: block;
                &.main-btns {
                    @media (max-width: 1025px) {
                        font-size: 15px;
                    }
                    &--map {
                        display: none;
                        margin-left: .75em;
                        background-color: #ff6347;
                        box-shadow: 0 0 0 6px rgba(255, 99, 71, .35);
                        align-items: center;
                        padding: .3em 1em;
                        border-radius: 50px;
                        font-weight: 600;
                        font-size: 15px;
                        color: white !important;
                        @media (max-width: 1025px) {
                            display: block;
                        }
                        &:hover {
                            background-color: $gray-txt;
                            box-shadow: 0 0 0 6px rgba(51, 51, 51, .35);
                        }
                    }
                }
            }
        }
    }
}

.project {
    padding: 5em 0 8em;
    background-color: #f2f2f2;
    @media (max-width: 992px) {
        padding: 2em 0 5em;
    }
    .row {
        align-items: center;
    }
    .box-txt-project {
        position: relative;
        width: 600px;
        padding: 2em;
        background-color: white;
        box-shadow: -5px 5px 30px rgba(0,0,0,.15);
        text-align: center;
        z-index: 1;
        @media (max-width: 992px) {
            width: 100%;
            padding: 0;
            background-color: transparent;
            box-shadow: none;
            margin-bottom: 2em;
        }
        .titles {
            text-align: left;
            h1 {
                text-align: left;
            }
        }
        .project-list {
            text-align: left;
            li {
                position: relative;
                padding-left: 1em;
                padding-top: .15em;
                padding-bottom: .15em;
                font-size: 1em;
                color: $primary-color;
                font-weight: 300;
                @media (max-width: 480px) {
                    font-size: .875em;
                }
                &:before {
                    content: "";
                    position: absolute;
                    width: 5px;
                    height: 5px;
                    background-color: $secondary-color;
                    border-radius: 100%;
                    top: 13px;
                    left: 0;
                }
            }
        }
        .main-btns {
            margin: 1em 0 0;
        }
        &__btns {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            .main-btns {
                display: block;
                margin-right: 1em;
                &--map {
                    background-color: #ff6347;
                    box-shadow: 0 0 0 6px rgba(255, 99, 71, .35);
                    color: white;
                    font-weight: 700;
                    &:hover {
                        background-color: $gray-txt;
                        box-shadow: 0 0 0 6px rgba(51, 51, 51, .35);
                    }
                }
            }
        }
    }
    .box-img-project {
        @media (max-width: 480px) {
            margin: 0 -15px;
        }
        img {
            display: block;
            width: 100%;
        }
    }
    .slick-dotted {
        &.slick-slider {
            margin-bottom: 0;
        }
    }

    .slick-arrow {
        width: 40px;
        height: 40px;
        background-color: $primary-color;
        &.slick-prev {
            left: 0;
            z-index: 1;
            &:before {
                content: "\F12F";
                font-family: bootstrap-icons!important;
                font-size: 25px;
                color: $secondary-color;
            }
        }
        &.slick-next {
            right: 0;
            &:before {
                content: "\F138";
                font-family: bootstrap-icons!important;
                font-size: 25px;
                color: $secondary-color;
            }
        }
    }

    .slick-dots {
        bottom: -40px;
        li {
            button {
                &:before {
                    font-size: 40px;
                    color: $primary-color;
                }
            }
        }
    }
}

.location {
    display: flex;
    align-items: center;
    background-color: $primary-color;
    @media (max-width: 992px) {
        flex-direction: column;
    }
    .container {
        position: absolute;
        left: 0;
        right: 0;
        @media (max-width: 992px) {
            position: relative;
        }
        .location-txt {
            text-align: center;
            @media (max-width: 480px) {
                padding: 3em 0;
            }
            h3 {
                font-size: 1.125em;
                color: white;
                font-weight: 300;
                @media (max-width: 480px) {
                    font-size: 1em;
                }
            }
            h2 {
                margin: 1em 0 0;
                font-size: 1.5em;
                font-weight: 600;
                color: white;
                @media (max-width: 480px) {
                    font-size: 1.25em;
                    margin: 1em 0 0;
                }
            }
            &__list {
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
                padding: 1em 0 0;
                li {
                    padding: .5em 0;
                    width: 33.33333%;
                    color: white;
                    p {
                        padding: 0;
                        font-size: .875em;
                        font-weight: 600;
                        @media (max-width: 480px) {
                            font-size: .75em;
                        }
                    }
                    span {
                        color: $secondary-color;
                        font-weight: .875em;
                        font-weight: 600;
                        @media (max-width: 480px) {
                            font-size: .75em;
                        }
                        i {
                            display: inline-block;
                            margin-left: .25em;
                        }
                    }
                }
            }
            .main-btns {
                margin-top: 2em;
                @media (max-width: 480px) {
                    padding: 1em;
                }
            }
        }
    }
    &__col {
        width: 50%;
        @media (max-width: 992px) {
            width: 100%;
        }
        iframe {
            position: relative;
            display: block;
            width: 100% !important;
            height: 600px;
            z-index: 1;
            @media (max-width: 992px) {
                height: 400px;
            }
            @media (max-width: 480px) {
                height: 350px;
            }
        }
    }
}

.gallery {
    position: relative;
    padding: 8em 0 3em;
    overflow: hidden;
    z-index: 1;
    @media (max-width: 480px) {
        padding: 3em 0 2em;
    }
    .container {
        text-align: center;
        .main-btns {
            margin-top: 3em;
        }
    }
    .titles {
        position: relative;
        text-align: center;
        z-index: 1;
    }
    .gallery-list {
        position: relative;
        margin: 2em auto 0;
        z-index: 1;
        @media (max-width: 768px) {
            width: auto;
        }
        @media (max-width: 480px) {
            margin: 1em -15px 0;
        }
        ul {
            li {
                padding: 0 8px;
                @media (max-width: 768px) {
                    padding: 0;
                }
                a {
                    position: relative;
                    display: block;
                    &:before {
                        content: "\F14D";
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background-color: rgba(0, 0, 0, .5);
                        opacity: 0;
                        top: 0;
                        left: 0;
                        font-size: 30px;
                        color: $secondary-color;
                        font-family: bootstrap-icons!important;
                        transition: .3s;
                    }
                    &:hover {
                        &:before {
                            opacity: 1;
                        }
                    }
                    img {
                        width: 100%;
                    }
                }
            }
        }
    }
    .gallery-videos {
        padding: 3em 0 0;
        .gallery-video-list {
            margin: 2em 0 0;
            @media (max-width: 768px) {
                width: auto;
            }
            @media (max-width: 480px) {
                margin: 1em -15px 0;
            }
            ul {
                li {
                    padding: 0 8px;
                    @media (max-width: 768px) {
                        padding: 0;
                    }
                    a {
                        position: relative;
                        display: block;
                        &:before {
                            content: "\F4F2";
                            position: absolute;
                            width: 100%;
                            height: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            background-color: rgba(0, 0, 0, .35);
                            top: 0;
                            left: 0;
                            font-size: 60px;
                            color: rgba(255, 255, 255, .5);
                            font-family: bootstrap-icons!important;
                            transition: .3s;
                        }
                        &:hover {
                            &:before {
                                color: $secondary-color;
                                background-color: rgba(0, 0, 0, .5);
                            }
                        }
                        img {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
    .slick-arrow {
        width: 40px;
        height: 40px;
        background-color: $primary-color;
        @media (max-width: 768px) {
            width: 40px;
            height: 40px;
            background-color: $primary-color;
        }
        &.slick-prev {
            left: -10px;
            z-index: 1;
            @media (max-width: 768px) {
                left: 0;
            }
            &:before {
                content: "\F12F";
                font-family: bootstrap-icons!important;
                font-size: 32px;
                color: $secondary-color;
                @media (max-width: 768px) {
                    font-size: 25px;
                    color: $secondary-color;
                }
            }
        }
        &.slick-next {
            right: -10px;
            @media (max-width: 768px) {
                right: 0;
            }
            &:before {
                content: "\F138";
                font-family: bootstrap-icons!important;
                font-size: 32px;
                color: $secondary-color;
                @media (max-width: 768px) {
                    font-size: 25px;
                    color: $secondary-color;
                }
            }
        }
    }
}

.contact {
    position: relative;
    padding: 8em 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    @media (max-width: 480px) {
        padding: 3em 0 0;
        background-image: none !important;
    }
    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(25, 68, 57, .5);
        top: 0;
        left: 0;
        z-index: 1;
        @media (max-width: 480px) {
            background-color: rgba(25, 68, 57, 1);
        }
    }
    .contact-txt {
        position: relative;
        z-index: 1;
        @media (max-width: 480px) {
            padding: 0 0 1em;
        }
        .titles {
            @media (max-width: 480px) {
                margin-bottom: 1em;
            }
        }
        p {
            font-size: 1.125em;
            color: white;
            font-weight: 300;
            @media (max-width: 480px) {
                font-size: 1em;
            }
        }
        h3 {
            padding: 1em 0 .75em;
            font-size: 1em;
            color: white;
            font-weight: 300;
        }
        .list-num {
            display: flex;
            align-items: center;
            padding: 0 0 1em;
            li {
                padding-right: 1em;
                a {
                    display: flex;
                    align-items: center;
                    padding: .35em 1em;
                    background-color: #25d366;
                    border-radius: 50px;
                    color: #103928;
                    font-weight: 600;
                    transition: .3s;
                    &:hover {
                        box-shadow: 0 0 0 6px rgba(37, 211, 102, .35);
                    }
                    i {
                        font-size: 22px;
                        margin-right: .35em;
                    }
                    i, span {
                        display: block;
                    }
                }
            }
        }
        .list-email {
            display: flex;
            align-items: center;
            li {
                padding-right: 1em;
                a {
                    display: flex;
                    align-items: center;
                    color: white;
                    text-decoration: underline;
                    transition: .3s;
                    @media (max-width: 480px) {
                        font-size: .875em;
                    }
                    &:hover {
                        color: $secondary-color;
                    }
                    i {
                        font-size: 22px;
                        margin-right: .35em;
                    }
                    i, span {
                        display: block;
                    }
                }
            }
        }
    }
    .contact-form {
        position: relative;
        z-index: 1;
        &__list {
            width: 80%;
            margin: 0 auto;
            padding: 3em 2em;
            background-color: white;
            box-shadow: -3px 3px 10px rgba(0, 0, 0, .15);
            @media (max-width: 480px) {
                width: auto;
                padding: 2em 1em;
                margin: 1em -15px 0;
            }
            li {
                padding: .5em 0;
                text-align: center;
                h2 {
                    padding: 0 0 1em;
                    font-size: 1.5em;
                    text-align: center;
                }
                input {
                    display: block;
                    width: 100%;
                    height: 50px;
                    padding-left: 1em;
                    background-color: #eeeeee;
                    border: none;
                    border-radius: 5px;
                    appearance: none;
                    color: $primary-color;
                    &::placeholder {
                        color: $primary-color;
                    }
                }
                textarea {
                    display: block;
                    width: 100%;
                    height: 150px;
                    padding-top: .5em;
                    padding-left: 1em;
                    background-color: #eeeeee;
                    border: none;
                    border-radius: 5px;
                    resize: none;
                    appearance: none;
                    color: $primary-color;
                    &::placeholder {
                        color: $primary-color;
                    }
                }
                input, textarea {
                    transition: .3s;
                    &:focus {
                        box-shadow: 0 0 0 3px rgba(25, 68, 57, .15);
                    }
                }
                button {
                    &.main-btns {
                        margin: .5em 0 0;
                        border: none;
                    }
                }
            }
        }
    }
}

footer {
    padding: 1.5em 0;
    background-color: $primary-color;
    .row {
        align-items: center;
    }
    img {
        display: block;
        width: 130px;
        @media (max-width: 768px) {
            margin: 0 auto;
        }
        @media (max-width: 480px) {
            width: 110px;
        }
    }
    .foot-social {
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width: 768px) {
            padding: .5em 0 1em;
        }
        li {
            padding: 0 .5em;
            span {
                display: block;
                color: white;
                font-weight: 300;
                @media (max-width: 480px) {
                    font-size: .875em;
                }
            }
            a {
                display: block;
                font-size: 1.25em;
                color: white;
                transition: .3s;
                &:hover {
                    color: $secondary-color;
                }
            }
        }
    }
    .copyright {
        display: flex;
        justify-content: flex-end;
        @media (max-width: 768px) {
            justify-content: center;
        }
        span {
            display: block;
            font-size: .875em;
            color: white;
            font-weight: 300;
            @media (max-width: 480px) {
                font-size: .75em;
            }
        }
    }
}

.msg-exito {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 90vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    text-align: center;
    @media (max-width: 480px) {
        height: 85vh;
    }
    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(25, 68, 57, .5);
        top: 0;
        left: 0;
    }
    .container {
        position: relative;
        z-index: 1;
        img {
            display: inline-block;
            margin-bottom: 1em;
            width: 180px;
        }
        h1 {
            font-size: 2.5em;
            color: white;
            font-weight: 600;
        }
    }
}

.site-construction {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(25, 68, 57, .8);
        top: 0;
        left: 0;
    }
    &__bg {
        display: block;
        width: 100%;
        height: 100vh;
    }
    &__content {
        position: absolute;
        text-align: center;
        padding: 1em;
        img {
            display: inline-block;
            width: 250px;
            @media (max-width: 480px) {
                width: 200px;
            }
        }
        h1 {
            padding: 1.5em 0 0;
            font-size: 2em;
            color: white;
            font-weight: 700;
            text-transform: uppercase;
            @media (max-width: 480px) {
                font-size: 1.5em;
            }
        }
        h2 {
            padding: .5em 0;
            font-size: 1.125em;
            color: white;
            font-weight: 300;
            @media (max-width: 480px) {
                font-size: 1em;
            }
            strong {
                font-weight: 700;
            }
        }
        .btn-whatsapp {
            display: flex;
            justify-content: center;
            padding: 1em 0;
            a {
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: $secondary-color;
                padding: .35em 1.5em;
                border-radius: 50px;
                color: $primary-color;
                font-weight: 700;
                transition: .3s;
                i {
                    display: block;
                    margin-right: .35em;
                    font-size: 1.5em;
                }
                span {
                    display: block;
                    font-size: 1.125em;
                }
                &:hover {
                    transform: translate(0,-2px);
                    box-shadow: 0 3px 15px rgba(141, 216, 21, .75);
                }
            }
        }
        h3 {
            padding: .35em 0;
            font-size: 1em;
            color: white;
            font-weight: 300;
        }
        ul {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: .5em 0;
            li {
                padding: 0 .5em;
                a {
                    display: block;
                    font-size: 1.5em;
                    color: white;
                    transition: .3s;
                    &:hover {
                        color: $secondary-color;
                    }
                }
            }
        }
    }
}

body {
    &.page-map {
        header {
            position: relative;
            background-color: white;
            box-shadow: 0 2px 16px rgba(0, 0, 0,.1);
            .row-header {
                &__col {
                    &--logo {
                        a {
                            opacity: 1;
                            visibility: visible;
                        }
                    }
                    &--menu {
                        .nav-menu {
                            &__item {
                                a {
                                    color: $gray-txt;
                                    &:hover {
                                        color: $secondary-color;
                                    }
                                }
                                &--whatsapp {
                                    a {
                                        color: #103928;
                                        &:hover {
                                            color: #103928;
                                        }
                                    }
                                }
                            }
                            &__social {
                                .nav-social {
                                    li {
                                        a {
                                            color: $gray-txt;
                                            &:hover {
                                                color: $secondary-color;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        #map {
            .ol-viewport {
                cursor: move;
            }
            .map-logo {
                position: absolute;
                width: 200px;
                height: 200px;
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: rgba(255, 255, 255, .85);
                top: 0;
                left: 100px;
                z-index: 1;
                @media (max-width: 769px) {
                    width: 150px;
                    height: 150px;
                    left: 30px;
                }
                img {
                    width: 150px;
                    @media (max-width: 769px) {
                        width: 120px;
                    }
                }
            }
            .content-details {
                position: absolute;
                width: 100%;
                background-color: rgba(25, 68, 57, .85);
                backdrop-filter: blur(3px);
                padding: 2em 0;
                bottom: 0;
                left: 0;
                z-index: 1;
                @media (max-width: 769px) {
                    padding: 1em 0;
                }
                .container {
                    display: flex;
                    justify-content: space-between;
                    @media (max-width: 769px) {
                        flex-direction: column;
                    }
                }
                .map-details {
                    display: flex;
                    align-items: center;
                    @media (max-width: 769px) {
                        padding-bottom: 1em;
                        flex-wrap: wrap;
                    }
                    &__item {
                        position: relative;
                        padding-right: 1.5em;
                        font-size: 1em;
                        color: white;
                        @media (max-width: 769px) {
                            width: 100%;
                            display: flex;
                            padding-right: .5em;
                            font-size: 14px;
                        }
                        a {
                            display: flex;
                            align-items: center;
                            padding-right: 1.5em;
                            color: white;
                            font-weight: 600;
                            text-decoration: underline;
                            transition: .3s;
                            @media (max-width: 769px) {
                                padding-right: 0;
                            }
                            &:hover {
                                color: $secondary-color;
                            }
                            i {
                                display: block;
                                margin-right: 0;
                                font-size: 2em;
                            }
                            span {
                                display: block;
                            }
                        }
                        &--1, &--2 {
                            padding-left: 1.85em;
                            @media (max-width: 769px) {
                                width: 50%;
                                margin-top: .5em;
                                margin-bottom: 1em;
                                padding-left: 2em;
                                font-size: 14px;
                            }
                            &:before {
                                content: "";
                                position: absolute;
                                width: 20px;
                                height: 20px;
                                border-radius: 100%;
                                left: 0;
                                top: 1px;
                                @media (max-width: 769px) {
                                    top: 0;
                                }
                            }
                        }
                        &--1 {
                            &:before {
                                background-color: #3fc94b;
                                box-shadow: 0 0 0 5px rgba(63, 201, 75, .35);
                            }
                        }
                        &--2 {
                            &:before {
                                background-color: #fe6446;
                                box-shadow: 0 0 0 5px rgba(254, 100, 70, .35);
                            }
                        }
                    }
                }   
                .map-contact {
                    display: flex;
                    align-items: center;
                    &__item {
                        padding-right: 1em;
                        &--whatsapp {
                            @media (max-width: 1025px) {
                                display: flex;
                                border: none;
                            }
                            a {
                                display: flex;
                                background-color: #25d366;
                                align-items: center;
                                padding: .3em 1em;
                                border-radius: 50px;
                                font-weight: 600;
                                font-size: 15px;
                                color: #103928;
                                transition: .3s;
                                @media (max-width: 769px) {
                                    font-size: 13px;
                                }
                                &:hover {
                                    color: $primary-color;
                                    box-shadow: 0 0 0 6px rgba(37, 211, 102, .35);
                                }
                                i, span {
                                    display: block;
                                }
                                i {
                                    font-size: 20px;
                                    margin-right: .35em;
                                    @media (max-width: 769px) {
                                        font-size: 16px;
                                    }
                                }
                            }
                        }
                        &--social {
                            .nav-social {
                                display: flex;
                                align-items: center;
                                li {
                                    padding-left: .75em;
                                    color: white;
                                    @media (max-width: 769px) {
                                        font-size: 14px;
                                        padding-left: 1em;
                                    }
                                    a {
                                        display: block;
                                        font-size: 20px;
                                        color: white;
                                        transition: .3s; 
                                        &:hover {
                                            color: $secondary-color;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .ol-popup {
            background-color: rgba(0, 0, 0, .75);
            backdrop-filter: blur(2px);
            border-radius: 5px;
            box-shadow: 0 0 20px rgba(0, 0, 0, .5);
            padding: 1.5em;
            border: none;
            font-size: 16px;
            color: white;
            #popup-closer {
                &:after {
                    content: "\F623";
                    font-family: bootstrap-icons!important;
                    font-size: 20px;
                    color: $secondary-color;
                }
            }
        }
        .ol-zoom {
            &.ol-unselectable {
                &.ol-control {
                    bottom: 130px;
                    right: 20px;
                    top: auto;
                    left: auto;
                    background-color: rgba(141, 216, 21, .85);
                    @media (max-width: 769px) {
                        bottom: 200px;
                    }
                    button {
                        width: 50px;
                        height: 50px;
                        background-color: white;
                        font-size: 1.25em;
                        color: $primary-color;
                    }
                }
            }
        }
        .ol-attribution {
            &.ol-unselectable {
                &.ol-control {
                    &.ol-uncollapsible {
                        display: none;
                    }
                }
            }
        }
    }
}